import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { DialogService } from 'src/app/api/util/dialog.service';
import { reduce, filter } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { RequestService } from 'src/app/api/request.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {

  public team_id;
  public team : any;

  public modelTeam : Model;

  constructor(
    public route : ActivatedRoute,
    public dialogService : DialogService,
    public requestService : RequestService
  ) {
    this.modelTeam = new Model('Team',requestService);

    this.team_id = this.route.snapshot.paramMap.get('team_id');
  

   }

  ngOnInit() {
    this.init();
  }

  async init( ){
    await this.dialogService.loadingProgres();

    this.modelTeam.api_functionModel(this.team_id,'pageAccounting').subscribe(
      response => {
        console.log(response)
        if(response['status'] == 'success')
          this.team = response['data'];

        this.dialogService.dimissLoaging();
      },
      error =>{
        this.dialogService.dimissLoaging();
      }
    )
  }

}
